body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #141E30; 
background: -webkit-linear-gradient(to right, #243B55, #141E30); 
background: linear-gradient(to right, #243B55, #141E30);
}
.container h1{
  display: flex;
  align-items: center;
  color: #FFF;
  font-size: 40px;
}

.containerInput{
  background-color: #dadada;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}
.containerInput input{
  background-color: transparent;
  color: rgba(0,0,0,0.8);
  border: none;
  outline: none;
  font-size: 20px;
  padding: 10px;
}
.containerInput::placeholder{
  color: rgba(0,0,0,0.8);
}
.containerInput button{
  background-color: transparent;
  border: none;
  font-size: 20px;
  padding: 5px;
  cursor: pointer;
  transition: transform 1.2s;
}
.containerInput button:hover{
  transform: scale(1.5);
}


.city{
  background-color: rgba(0, 0, 0, 0.356);
  padding: 5px;
}
.main{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: transparent;
  box-shadow: 1px 1px 10px 5px rgba(0,0,0,0.5);
  margin-top: 20px;
  border-radius: 10px;
  padding: 10px;
  max-width: 400px;
}
.main span{
  color: white;
  font-size: 20px;
  margin-top: 10px;
  text-align: center;
}
.main img{
  width: 50%;
  border-radius: 50%;
  margin-top: 10px;
  margin-bottom: 20px;
}




.socialMedias{
  margin-top: 20px;
  
}
.socialMedias a{
  font-size: 30px;
  margin: 20px;
  cursor: pointer;
  transition: 0.7s;
}
.socialMedias a:hover{
  opacity: 0.5;
}



.followers{
  display: flex;
  text-align: center;
  margin-top: 10px;
}

.followers span{
  font-size: 15px;
  margin: 10px;
}

@media (max-width: 420px){
  .main{
    width: 350px;
  }
  .container h1{
    font-size: 25px;
    margin-top: 0;
  }
  .containerInput input{
    padding: 10px 2px;
  }
}

@media (max-width: 320px){
  .main{
    width: 300px;
  }
  .container h1{
    font-size: 20px;
  }
  .containerInput input{
    padding: 2px;
  }
  .main img{
    width: 40%;
  }
}
